.App {
  text-align: center;
}

body{
  overflow-y: hidden;
}

.temperature-holder{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}



canvas{
  position: fixed;
  top: 0;
  left:0;
  z-index: -10;
  width:100vw;
  height: 100vh;


}

.top-click{
  height: 10%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;

  border-bottom: 2px solid white;
}

.buttom-click{
  height: 10%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: rgba(2,2,2,0.1);
  border-top: 2px solid white;
}

.on-hover:hover{
  background-color: rgba(2,2,2,0.3);
  cursor: pointer;
}
.on-hover-top:hover{
  background-color: rgba(2,2,2,0.2);
  cursor: pointer;
}

.temp-output{
  position: absolute;
  top: 38%;
  left: 48%;
  font-size: 72px;

}

ReactTouchEvents{
  border: 1px solid red;
  background: grey;
  width: 100vw;
  height:100vh;
}

.icon-up{
  display: inline-block;
  font-size: 36px;
  margin-top: 27px;
}

.icon-down{
  font-size: 36px;
  margin-top: 27px;

}

@media (max-height: 700px){


  .icon-up,
  .icon-down{
    margin-top: 10px;
  }

.buttom-click {
  height: 11%;

}


.top-click{
  height: 11%;
}

.temp-output{
  left: 40%;
  top: 35%;
  font-size: 64px;


}

}


